import './App.css';
import Header from "./MyComponents/Header";
import { Todos } from "./MyComponents/Todos";
import { Footer } from "./MyComponents/Footer";
import { AddTodo } from "./MyComponents/AddTodo";
import { About } from "./MyComponents/About";
import React, { useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Typewriter from 'typewriter-effect';
import Alert from "./MyComponents/Alert";

function App() {
  const [alert, setAlert] = useState(null);

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }

  const [mode, setMode] = useState('primary');
  const toggleMode = () => {
    if (mode === 'primary') {
      setMode('dark');
      document.body.style.backgroundColor = 'grey';        // in place of grey #042743
      showAlert("Dark theme has been enabled", "success");
    }
    else {
      setMode('primary');
      document.body.style.backgroundColor = 'pink';
      showAlert("Light theme has been enabled", "success");
    }
  }

  let initTodo;
  if (localStorage.getItem("todos") === null) {
    initTodo = []
  }
  else {
    initTodo = JSON.parse(localStorage.getItem("todos"));
  }
  const onDelete = (todo) => {
    setTodos(todos.filter((e) => {
      return e !== todo;
    }));

    localStorage.removeItem(`${todo.sno}`);

    localStorage.setItem("todos", JSON.stringify(todos));
    showAlert("Your todo item has been deleted", "success");
  }

  const addTodo = (title, desc) => {
    let sno;
    if (todos.length === 0) {
      sno = 0;
    }
    else {
      sno = todos[todos.length - 1].sno + 1;
    }
    const myTodo = {
      sno: sno,
      title: title,
      desc: desc
    }
    setTodos([...todos, myTodo]);
  }

  const [todos, setTodos] = useState(initTodo);
  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos])

  return (
    <>
      <Router>
        <Header title="To Do List" searchBar={false} mode={mode} toggleMode={toggleMode} />
        <Alert alert={alert} />
        <Switch>
          <Route exact path="/" render={() => {
            return (
              <>

                {/* TypeWriter Effect */}
                <div className="container">
                  <div className="text-center my-3" style={{ color: mode === 'primary' ? '#042743' : '#85144B', fontSize: '3rem', paddingBottom: '2rem' }}>
                    <Typewriter
                      options={{
                        autoStart: true,
                        loop: true,
                        delay: 40,
                        strings: [
                          "Welcome to ToDoList.com!",
                          "Hope you are doing good.",
                          "Have fun with us :)"
                        ],
                      }} />
                  </div>
                </div>
                {/* TypeWriter Effect */}

                <AddTodo addTodo={addTodo} mode={mode} showAlert={showAlert} />
                <Todos todos={todos} onDelete={onDelete} mode={mode} />
              </>)
          }}>
          </Route>
          <Route exact path="/about">
            <About mode={mode} />
          </Route>
          <Redirect to="/" />
        </Switch>
        <Footer />
      </Router>
    </>
  );
}

export default App;
