import React from 'react'
import { TodoItem } from "./TodoItem";

export const Todos = (props) => {
    document.title = 'To Do List | Home';
    let myStyle = {
        minHeight: "70vh",
        margin: "40px auto"
    }
    return (
        <div className="container" style={myStyle}>
            <h3 className="text-center my-3" style={{ color: props.mode === 'primary' ? '#4B1485' : 'white' }}><u>To Do List</u></h3>
            <br />
            {props.todos.length === 0 ? <div className="text-center">Nothing to display. Try adding one!</div> :
                props.todos.map((todo) => {
                    return (<TodoItem todo={todo} key={todo.sno} onDelete={props.onDelete} mode={props.mode} />)
                })
            }
        </div>
    )
}
