import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './AddTodo.css'

export const AddTodo = ({ addTodo, mode, showAlert }) => {
    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");
    const submit = (e) => {
        e.preventDefault();
        if (!title || !desc || (title.charAt(0) === " ") || (desc.charAt(0) === " ")) {
            alert("Title or Description cannot be blank or cannot begin with a space.")
        }
        else {
            addTodo(title, desc);
            setTitle("");
            setDesc("");
            showAlert("Your todo item has been added", "success");
        }
    }
    return (
        <div className="container my-3">
            <h3 className="text-center" style={{ color: mode === 'primary' ? '#4B1485' : 'white' }}><u>Add a Todo</u></h3>
            <br /><div id="emailHelp" className="form-text" style={{ color: mode === 'primary' ? 'red' : 'yellow' }}>Fields marked with * are required. <Link style={{ color: mode === 'primary' ? '' : '#194888' }} to="#" type="button" data-bs-toggle="collapse" data-bs-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">
                Learn more
            </Link>
                <div className="collapse text-center" id="collapseExample">
                    <div className="card card-body my-3" style={{ backgroundColor: mode === 'primary' ? '' : '#13466e', color: mode === 'primary' ? 'black' : 'white' }}>
                        In case you want to add a todo item without description, press enter in the 'Todo Description' field.
                    </div>
                </div></div><br />
            <form onSubmit={submit}>
                <div className="mb-3">
                    <label htmlFor="title" className="form-label"><b>Todo Title *</b></label>
                    <input type="text" value={title} onChange={(e) => setTitle(e.target.value)} className="form-control" id="title" aria-describedby="emailHelp" placeholder="Enter the title..." style={{ backgroundColor: mode === 'primary' ? '' : '#13466e', color: mode === 'primary' ? '' : 'white' }} />
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label"><b>Todo Description *</b></label>
                    <textarea value={desc} onChange={(e) => setDesc(e.target.value)} className="form-control" id="desc" rows="3" placeholder="Enter the description..." style={{ backgroundColor: mode === 'primary' ? '' : '#13466e', color: mode === 'primary' ? '' : 'white' }} ></textarea>
                </div>
                {/* disabled={!title || !desc || (title.charAt(0) === " ") || (desc.charAt(0) === " ")} */}
                <button type="submit" className={`btn btn-sm my-2 btn-${mode === 'primary' ? 'outline-secondary' : 'outline-success'}`} data-bs-toggle="tooltip" data-bs-placement="bottom" title="Add a todo item">Add Todo</button>
            </form>
        </div >
    )
}
