import React from 'react'
import PropTypes from 'prop-types'
import { Link, NavLink } from "react-router-dom";
import './Header.css'

// @material-ui in place of @mui
// core in place of material
// icons in place of icons-material
import Avatar from '@material-ui/core/Avatar';
import AssignmentIcon from '@material-ui/icons/Assignment';

export default function Header(props) {
    return (
        // style={{ position: 'sticky', top: '0' }}
        <nav className={`navbar navbar-expand-lg navbar-dark bg-${props.mode}`}>
            <div className="container-fluid" >
                <Link className="navbar-brand" to="/">
                    <Avatar style={{ backgroundColor: '#4caf50' }} variant={props.mode === 'primary' ? '' : 'rounded'}>
                        <AssignmentIcon style={{ color: props.mode === 'primary' ? '' : 'black' }} />
                    </Avatar>
                </Link>
                <Link className="navbar-brand" to="/">{props.title}</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <NavLink exact activeClassName="active" className="nav-link" aria-current="page" to="/">Home</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink activeClassName="active" className="nav-link" to="/about">About</NavLink>
                        </li>
                    </ul>
                    {props.searchBar ? <form className="d-flex">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className="btn btn-outline-success" type="submit">Search</button>
                    </form> : ""}
                    <div className={`form-check form-switch text-${props.mode === 'primary' ? 'light' : 'light'}`}>
                        <input className="form-check-input" style={{ cursor: 'pointer' }} onClick={props.toggleMode} type="checkbox" role="switch" id="flexSwitchCheckDefault" data-bs-toggle="tooltip" data-bs-placement="bottom" title="Toggle dark theme" />
                        <label className="form-check-label" htmlFor="flexSwitchCheckDefault" style={{ cursor: 'pointer' }}>{props.mode === 'primary' ? 'Enable' : 'Disable'} Dark Mode</label>
                    </div>
                </div>
            </div>
        </nav>
    )
}

Header.defaultProps = {
    title: "Your Title Here",
    searchBar: true
}

Header.propTypes = {
    title: PropTypes.string,
    searchBar: PropTypes.bool.isRequired
}