import React from 'react'

export const About = (props) => {
    document.title = 'To Do List | About';
    let myStyle = {
        minHeight: "70vh",
        margin: "0px 15px",
        fontSize: '20px'
    }
    return (
        <div style={myStyle}>
            <h1 className="text-center my-3" style={{ color: props.mode === 'primary' ? 'green' : '#4B1485' }}><u>About Us</u></h1>
            <br />
            <div style={{ color: props.mode === 'primary' ? '' : 'WHITE' }}><h4>Welcome!</h4>We are constantly working towards improving our services.<br /><br />Thanks for visiting.</div>
        </div>
    )
}
